<script>
  import browserCheck from '../_utils/browser-check.js';
  import contains from '../_utils/contains.js';

  const knownBrowsers = [
    { name: 'mobile', isCurrentBrowser: browserCheck.isMobile() },
    { name: 'chrome', isCurrentBrowser: browserCheck.isChrome() },
    { name: 'firefox', isCurrentBrowser: browserCheck.isFirefox() },
    { name: 'safari', isCurrentBrowser: browserCheck.isSafari() },
    { name: 'opera', isCurrentBrowser: browserCheck.isOpera() },
    { name: 'ie', isCurrentBrowser: browserCheck.isIE() },
    { name: 'edge', isCurrentBrowser: browserCheck.isEdge() },
    { name: 'blink', isCurrentBrowser: browserCheck.isBlink() }
  ];

  const currentBrowser = knownBrowsers.find(b => b.isCurrentBrowser === true);

  export let browsers = []; // 'chrome', 'firefox', 'safari', 'opera', 'ie', 'edge', 'mobile', 'blink'
  export let only = false;
  export let except = false;

  $: allowedBrowsers = browserCheck.allBrowsers
    .reduce((acc, browser) => {
      if (except) {
        if (!contains(browser, browsers)) acc.push(browser);
      } else if (only) {
        if (contains(browser, browsers)) acc.push(browser);
      } else {
        // never goes here
      }

      return acc;
    }, []);

  $: restrictedBrowsers = browserCheck.allBrowsers
    .reduce((acc, browser) => {
      if (except) {
        if (contains(browser, browsers)) acc.push(browser);
      } else if (only) {
        if (!contains(browser, browsers)) acc.push(browser);
      } else {
        // never goes here
      }

      return acc;
  }, []);

  $: shouldRender = currentBrowser
    && (
      !contains(currentBrowser.name, restrictedBrowsers)
      || contains(currentBrowser.name, allowedBrowsers)
    );
</script>

{#if shouldRender}
<slot></slot>
{/if}